<template>
  <div id="app">
    <!-- HW Ïª¥Ìè¨ÎÑåÌä∏Ïóê Îç∞Ïù¥ÌÑ∞, Ï†ÄÏû• Ìï®Ïàò, ÎπÑÎ∞ÄÎ≤àÌò∏ Ï≤¥ÌÅ¨ Ìï®ÏàòÎ•º propsÎ°ú Ï†ÑÎã¨Ìï©ÎãàÎã§. -->
    <HW 
      :duty-data="dutyData" 
      :save-duty-entry="saveDutyEntry"
      :check-password="checkPassword"
    />
  </div>
</template>

<script>
import HW from './components/HW.vue'
import { initializeApp } from "firebase/app";
import { getMessaging, getToken, onMessage } from "firebase/messaging";
import { 
  getFirestore, 
  collection, 
  onSnapshot, 
  query, 
  doc, 
  setDoc,
  where,
  getDocs
} from "firebase/firestore";

export default {
  name: 'App',
  components: {
    HW
  },
  data() {
    return {
      db: null, // Firestore Ïù∏Ïä§ÌÑ¥Ïä§Î•º Ï†ÄÏû•
      dutyData: [], // FirestoreÏóêÏÑú Í∞ÄÏ†∏Ïò® Ï∫òÎ¶∞Îçî Îç∞Ïù¥ÌÑ∞Î•º Ï†ÄÏû•Ìï† Î∞∞Ïó¥
      unsubscribe: null // Ïã§ÏãúÍ∞Ñ Î¶¨Ïä§ÎÑà Ìï¥Ï†ú Ìï®ÏàòÎ•º Ï†ÄÏû•
    }
  },
  methods: {
    // ÎπÑÎ∞ÄÎ≤àÌò∏ Í≤ÄÏ¶ù Ìï®Ïàò
    async checkPassword(inputPassword) {
      if (!this.db) {
        console.error("Firestore DB is not initialized.");
        return { success: false, message: "DB Ï¥àÍ∏∞Ìôî Ïò§Î•ò." };
      }
      
      try {
        // 1. password ÌïÑÎìúÍ∞Ä ÏûÖÎ†•Îêú Í∞íÍ≥º ÏùºÏπòÌïòÎäî Î¨∏ÏÑúÎ•º Ï∞æÎäî ÏøºÎ¶¨ ÏÉùÏÑ±
        const passwordQuery = query(
          collection(this.db, "editpw"),
          where("password", "==", inputPassword)
        );

        // 2. ÏøºÎ¶¨ Ïã§Ìñâ
        const querySnapshot = await getDocs(passwordQuery);

        // 3. Í≤∞Í≥º ÌôïÏù∏: ÏùºÏπòÌïòÎäî Î¨∏ÏÑúÍ∞Ä 1Í∞ú Ïù¥ÏÉÅ Ï°¥Ïû¨ÌïòÎ©¥ ÏÑ±Í≥µ
        if (!querySnapshot.empty) {
          // ÏùºÏπòÌïòÎäî Î¨∏ÏÑúÎ•º Ï∞æÏïòÏäµÎãàÎã§. (Ïã§Ï†ú Îç∞Ïù¥ÌÑ∞Îäî Ï≤´ Î≤àÏß∏ Î¨∏ÏÑúÏùò ÌïÑÎìúÎ•º ÏÇ¨Ïö©Ìï† ÌïÑÏöî ÏóÜÏù¥ Ï°¥Ïû¨ Ïó¨Î∂ÄÎßå ÌôïÏù∏)
          return { success: true };
        } else {
          // ÏùºÏπòÌïòÎäî Î¨∏ÏÑúÍ∞Ä ÏóÜÍ±∞ÎÇò ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÌãÄÎ¶∞ Í≤ΩÏö∞
          return { success: false, message: "ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§." };
        }
      } catch (error) {
        console.error("ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
        return { success: false, message: `ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ Ï§ë Ïò§Î•ò Î∞úÏÉù: ${error.message}` };
      }
    },

    // ÎãπÏßÅ Ï†ïÎ≥¥Î•º FirestoreÏóê Ï†ÄÏû• ÎòêÎäî ÏàòÏ†ïÌïòÎäî Ìï®Ïàò
    async saveDutyEntry(entry, docIdToUpdate) {
      if (!this.db) {
        console.error("Firestore DB is not initialized.");
        return false;
      }

      // Ï†ÄÏû• Ï†ÑÏóê date ÌïÑÎìúÎ•º Ïà´ÏûêÎ°ú Î≥ÄÌôò (App.vueÏóêÏÑú Ïà´ÏûêÎ°ú Í∞ÄÏ†ïÌñàÏúºÎØÄÎ°ú)
      const dataToSave = {
        ...entry,
        date: parseInt(entry.date, 10), // 'YYMMDD' Î¨∏ÏûêÏó¥ÏùÑ Ïà´ÏûêÎ°ú Î≥ÄÌôò
      };

      try {
        if (docIdToUpdate) {
          // Í∏∞Ï°¥ Î¨∏ÏÑú ÏóÖÎç∞Ïù¥Ìä∏ (updateDoc ÎåÄÏã† setDocÏúºÎ°ú Ï†ÑÏ≤¥ ÌïÑÎìú ÎçÆÏñ¥Ïì∞Í∏∞)
          const docRef = doc(this.db, "calendar", docIdToUpdate);
          await setDoc(docRef, dataToSave);
          return true; // ÏàòÏ†ï ÏÑ±Í≥µ
        } else {
          // ÏÉà Î¨∏ÏÑú ÏÉùÏÑ± (Î¨∏ÏÑú IDÎ•º dateÏôÄ categoryÎ°ú Ï°∞Ìï©ÌïòÏó¨ Í≥†Ï†ï ID ÏÇ¨Ïö©)
          // Í∏∞Ï°¥ Î¨∏ÏÑúÏùò IDÍ∞Ä ÎûúÎç§ IDÏù¥ÎØÄÎ°ú, Ïó¨Í∏∞ÏÑúÎäî ÏÉà Î¨∏ÏÑúÎ•º ÎßåÎì§ Îïå dateÎ•º ÌÇ§Î°ú ÌïòÎäî Í≥†Ï†ï IDÎ•º ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÍ≥†, setDocÏúºÎ°ú ÏÉà Î¨∏ÏÑúÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§.
          // FirebaseÏóêÏÑú ÎûúÎç§ IDÍ∞Ä Ìï†ÎãπÎêòÎèÑÎ°ù setDoc ÏóÜÏù¥ doc(db, 'calendar', newId)Î•º ÏÇ¨Ïö©ÌïòÏó¨ IDÎ•º Î®ºÏ†Ä ÎßåÎì§Í±∞ÎÇò,
          // Í∞ÄÏû• Í∞ÑÎã®ÌïòÍ≤åÎäî setDocÏóê ID ÏóÜÏù¥ docRefÎ•º ÏÉùÏÑ±ÌïòÏó¨ FirestoreÍ∞Ä ÎûúÎç§ IDÎ•º Ìï†ÎãπÌïòÎèÑÎ°ù Ìï©ÎãàÎã§.
          
          // Ïó¨Í∏∞ÏÑú Ïö∞Î¶¨Îäî setDocÏùÑ ÏÇ¨Ïö©ÌïòÎêò, Î¨∏ÏÑú IDÎ•º Î™ÖÏãúÌïòÏßÄ ÏïäÏúºÎ©¥ setDocÏùÄ addDocÏ≤òÎüº ÏûëÎèôÌïòÏßÄ ÏïäÏäµÎãàÎã§.
          // Îî∞ÎùºÏÑú, ÏÉà Î¨∏ÏÑúÎ•º ÏÉùÏÑ±ÌïòÎ†§Î©¥ addDocÏùÑ ÏÇ¨Ïö©ÌïòÍ±∞ÎÇò, setDocÏùÑ ÏÇ¨Ïö©Ìï† Í≤ΩÏö∞ Í≥†Ïú† IDÎ•º ÏÉùÏÑ±Ìï¥Ïïº Ìï©ÎãàÎã§.
          
          // Ïó¨Í∏∞ÏÑúÎäî Î¨∏ÏÑú IDÍ∞Ä ÏóÜÏúºÎØÄÎ°ú FirestoreÏóêÏÑú ÏûêÎèôÏúºÎ°ú ÏÉùÏÑ±ÎêòÎèÑÎ°ù Ìï©ÎãàÎã§.
          const newDocRef = doc(collection(this.db, "calendar"));
          await setDoc(newDocRef, dataToSave);
          return true; // ÏÉùÏÑ± ÏÑ±Í≥µ
        }
      } catch (error) {
        console.error("Firestore Ï†ÄÏû• Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
        return false;
      }
    }
  },
  mounted() {
    // Firebase ÏÑ§Ï†ï
    const firebaseConfig = {
      apiKey: "AIzaSyDMjFwCNFE72bctk4BNodId8fLLXMQ_Ulw",
      authDomain: "hello-notification-121eb.firebaseapp.com",
      projectId: "hello-notification-121eb",
      storageBucket: "hello-notification-121eb.firebasestorage.app",
      messagingSenderId: "17313072285",
      appId: "1:17313072285:web:ddbd170a5b062f236fc074",
      measurementId: "G-8W89T2V4CF"
    };

    // Firebase Ï¥àÍ∏∞Ìôî Î∞è Îç∞Ïù¥ÌÑ∞ Î°úÎî© Î°úÏßÅ Ï∂îÍ∞Ä ---
    const app = initializeApp(firebaseConfig);
    // const db = getFirestore(app);
    // const calendarColRef = collection(db, "calendar");
    this.db = getFirestore(app);
    const calendarColRef = collection(this.db, "calendar");

    //260108 ÏÉàÎ°ú Ï∂îÍ∞Ä ÏãúÏûë
    // ÎÇ†Ïßú Î≤îÏúÑ Í≥ÑÏÇ∞ (Ïù¥Ï†ÑÎã¨ 1Ïùº ~ Îã§ÏùåÎã¨ ÎßêÏùº)
    const now = new Date();
    
    // Ïù¥Ï†ÑÎã¨ 1Ïùº Íµ¨ÌïòÍ∏∞ (YYMMDD ÌòïÏãù Ïà´Ïûê)
    const prevMonth = new Date(now.getFullYear(), now.getMonth() - 1, 1);
    const startRange = parseInt(
      String(prevMonth.getFullYear()).slice(-2) + 
      String(prevMonth.getMonth() + 1).padStart(2, '0') + "01"
    );

    // Îã§ÏùåÎã¨ ÎßêÏùº Íµ¨ÌïòÍ∏∞ (Îã§ÏùåÎã¨ + 1Ïùò 0Î≤àÏß∏ ÎÇ† = Îã§ÏùåÎã¨ ÎßêÏùº)
    const nextMonthEnd = new Date(now.getFullYear(), now.getMonth() + 2, 0);
    const endRange = parseInt(
      String(nextMonthEnd.getFullYear()).slice(-2) + 
      String(nextMonthEnd.getMonth() + 1).padStart(2, '0') + 
      String(nextMonthEnd.getDate()).padStart(2, '0')
    );

    // ÌïÑÌÑ∞ÎßÅÎêú Ïã§ÏãúÍ∞Ñ Î¶¨Ïä§ÎÑà ÏÑ§Ï†ï
    const q = query(
      calendarColRef,
      where("date", ">=", startRange),
      where("date", "<=", endRange)
    );
    
    // Ïã§ÏãúÍ∞Ñ Î¶¨Ïä§ÎÑà ÏÑ§Ï†ï (onSnapshot)
    //260108 ÏÉàÎ°ú Ï∂îÍ∞Ä Ï¢ÖÎ£å
    //this.unsubscribe = onSnapshot(query(calendarColRef), (snapshot) => {
    this.unsubscribe = onSnapshot(q, (snapshot) => {
      this.dutyData = snapshot.docs.map(doc => {
        const data = doc.data();
        return {
          id: doc.id,  // Î¨∏ÏÑú ID (ÌïÑÏöîÏãú ÏÇ¨Ïö©)
          category: data.category,
          date: String(data.date), // HW.vueÏùò ÎπÑÍµêÎ•º ÏúÑÌï¥ Î¨∏ÏûêÏó¥Î°ú Î≥ÄÌôòÌïòÏó¨ Ï†ÑÎã¨
          holidayYn: data.holidayYn,
          meYn: data.meYn,
          name: data.name
        };
      });
      console.log("Firestore calendar data loaded successfully.", this.dutyData.length, "items.");
    }, (error) => {
      console.error("Error fetching Firestore data:", error);
    });

    const messaging = getMessaging(app);

    // Ìè¨Í∑∏ÎùºÏö¥Îìú Î©îÏãúÏßÄ ÏàòÏã†
    onMessage(messaging, (payload) => {
      console.log('Message received: ', payload);
    });

    // ÌÜ†ÌÅ∞ ÏöîÏ≤≠
    getToken(messaging, { vapidKey: 'BLYnDWIi8AbuQdrJx1ct6EU5hi6yz6dyG4GxFgLvb_2G42mMjdLwF9i_PkcN8csNR3HynhqVu9ZO92_Am_RcuVg' })
      .then((currentToken) => {
        if (currentToken) {
          console.log("Firebase Token:", currentToken);
        } else {
          console.log('No registration token available. Request permission to generate one.');
        }
      })
      .catch((err) => {
        console.log('An error occurred while retrieving token: ', err);
      });
  },
  beforeUnmount() {
    // Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÌååÍ¥¥ÎêòÍ∏∞ Ï†ÑÏóê Ïã§ÏãúÍ∞Ñ Î¶¨Ïä§ÎÑàÎ•º Ìï¥Ï†úÌï©ÎãàÎã§.
    if (this.unsubscribe) {
      this.unsubscribe();
    }
  }
}
</script>

